.results {
  &__avatars {
    display: flex;
    align-items: center;
    justify-content: space-around;
    padding: 0;
    margin: 0;
    list-style: none;
  }
  &__controls {

  }

  &__controls-row {
    margin: 10px 0;
  }
}

.results-avatars-item {
  width: 100px;
  height: 100px;
  border: 2px solid #66ffff;
  overflow: hidden;
  margin-bottom: 10px;

  border-radius: 3px;
  background-size: cover;
}

.results-info {
  padding: 10px;

  background: #0d9ab2;

  &__title {
    margin: 5px 15px;

    color: #fff;
    font-size: 21px;
  }

  &__content {
    display: flex;
    align-items: center;
    justify-content: space-around;
    padding: 0;
    margin: 0;
    list-style: none;
  }
}

.results-info-content-item {
  color: #014461;
  font-size: 15px;
  text-align: center;

  &__title {
    margin: 0;
  }

  &__value {
    margin: 0;
  }
}




