.game {
  position: absolute;
  top: 0;
  left: 0;

  width: 100%;

  &__header {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
  }

  &__moves {
    width: 80px;

    background: #1ea7b4;
    border-radius: 0 0 10px 10px;
    border-left: 1px solid #01464e;
    border-bottom: 1px solid #01464e;
    border-right: 1px solid #01464e;
  }

  &__moves-title {
    margin: 10px 0 5px;

    color: #013c46;
    font-size: 13px;
    font-weight: 600;
  }

  &__moves-value {
    @include text-stroke(1px, #000);

    margin: 0 0 10px;

    font-size: 30px;
    font-weight: 600;
    color: #ffe83d;
  }

  &__preloader {
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.game-player {
  position: relative;

  width: 110px;
  height: 80px;
  display: flex;
  margin-top: 5px;

  background: #1294a3;

  &_user {
    border-radius: 0 10px 10px 0;
  }

  &_rival {
    border-radius: 10px 0 0 10px;
  }

  &__info {
    width: 60px;
    padding: 4px 0 0 4px;
    box-sizing: border-box;
    overflow: hidden;

    background: #27bbc8;
  }

  &__avatar {
    width: 48px;
    height: 48px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid #01464e;
    overflow: hidden;

    border-radius: 10px;
    background-size: cover;
  }

  &__name {
    margin: 5px 0 0;
  }

  &__score {
    width: 50%;
  }

  &__score-title {
    margin: 20px 0 5px;

    font-size: 15px;
    font-weight: 600;
    color: #ffe83d;
    text-shadow: -1px -1px 1px rgba(0, 0, 0, .5);
  }

  &__score-value {
    @include text-stroke(1px, #000);

    margin: 5px 0 0;

    color: #ffe83d;
    font-weight: 600;
    font-size: 20px;
  }

  &__timer {

  }
}

.game-player-status {
  @mixin game-player-status-speech {
    @include retina-sprite($speech-group);

    display: block;
    margin-top: 10px;
    content: '';
  }

  position: absolute;
  bottom: -44px;

  display: flex;

  &_user {
    right: -110px;

    &::before {
      @include game-player-status-speech;
    }
  }

  &_rival {
    left: -110px;

    &::after {
      @include game-player-status-speech;
      transform: scaleX(-1);
    }
  }

  &__content {
    width: 120px;
    height: 44px;

    text-align: center;
    background: #fff;
    border-radius: 10px;
    color: #013942;
  }

  &__title {
    margin: 5px 0 0;
    font-size: 17px;
  }

  &__timer {
    font-size: 14px;
  }
}

.game-field {
  &_disabled {
    opacity: .7;

    canvas {
      pointer-events: none;
    }
  }
}



