& {
  font-family: $font-family-default;
  font-weight: 400;
  line-height: 1.2;
}

@include retina-sprites-custom($retina-groups);

.sprite {
  display: inline-block;
  vertical-align: middle;
}

.logo {
  margin-bottom: 10px;
}

.inner {
  background: #15afc5;
  height:100vh;
  box-sizing: border-box;
  padding: 10px;
  text-align: center;

  &_game {
    background: url($images-path + 'bg.jpg') no-repeat 0 0;
    background-size: cover;

    padding: 0;
  }
}



