.btn {
  position: relative;

  display: inline-block;
  box-sizing: border-box;
  border: 0;

  white-space: nowrap;
  text-decoration: none;
  outline: none;
  cursor: pointer;
  user-select: none;

  &_simple {
    $height: 46px;

    padding: 0 12px;
    height: $height;
    line-height: $height;

    color: #155100;
    font-weight: 600;
    font-family: $font-family-default;
    box-shadow: inset 0 2px 1px 3px #b3ff50, inset 0 -1px 1px 1px #2a9f00;
    font-size: 20px;
    border: 1px solid #1f5500;
    border-radius: 10px;
    background: linear-gradient(to bottom, #5cb80b 0%, #93f123 49%, #5cd403 50%, #65e905 100%);

    &:hover {
      background: #65e905;
    }

    &:active {
      background: #51b305;
    }
  }

  &_accent {
    $height: 46px;

    padding: 0 12px;
    height: $height;
    line-height: $height;

    color: #912900;
    font-weight: 600;
    font-family: $font-family-default;
    box-shadow: inset 0 2px 1px 3px #ffa900, inset 0 -1px 1px 1px #ffa200;
    font-size: 20px;
    border: 1px solid #853000;
    border-radius: 10px;
    background: linear-gradient(to bottom, #ff8409 0%, #ffda20 49%, #ffac00 50%, #ffd700 100%);

    &:hover {
      background: #ffac00;
    }

    &:active {
      background: #ff8409;
    }
  }

  &[disabled] {
    opacity: .5;
    pointer-events: none;
  }
}



