.tutor {
  margin-bottom: 20px;

  &__header {
    padding: 5px;

    background: rgba(0, 62, 71, .4);
    border-radius: 10px 10px 0 0;
    font-size: 16px;
    color: #fff;
  }

  &__content {
    padding: 20px 0;

    background: rgba(0, 62, 71, .2);
    text-align: center;
  }

  &__list {
    width: 285px;
    padding: 0;
    margin: 0;
    display: inline-flex;
    align-items: center;
    justify-content: space-between;
  }

  &__list-item {
    position: relative;
    display: block;

    &_shift-left {
      animation: {
        name: #{$prefix}-shift-left;
        duration: 1s;
        fill-mode: forwards;
        timing-function: linear;
      }
    }

    &_shift-left-static {
      left: -100px;
    }

    &_shift-right {
      animation: {
        name: #{$prefix}-shift-right;
        duration: 1s;
        fill-mode: forwards;
        timing-function: linear;
      }
    }

    &_pulse {
      animation: {
        name: #{$prefix}-pulse;
        duration: .5s;
        fill-mode: forwards;
        iteration-count: infinite;
        timing-function: linear;
      }
    }
  }
}



