@keyframes #{$prefix}-shift-left {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(-100px);
  }
}

@keyframes #{$prefix}-shift-right {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(100px);
  }
}

@keyframes #{$prefix}-pulse {
  0% {
    transform: scale(1);
  }

  25% {
    transform: scale(.7);
  }

  50% {
    transform: scale(1);
  }

  75% {
    transform: scale(1.1);
  }

  100% {
    transform: scale(1);
  }
}



