.rival-find {
  &__content {
    padding: 0 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__user-avatar {
    border: 2px solid #66ffff;
    overflow: hidden;
    width: 90px;
    height: 90px;
    display: flex;
    align-items: center;
    justify-content: center;

    border-radius: 3px;
    background-size: cover;
  }

  &__user-name {
    margin: 10px 0 0;

    color: #012d42;
    font-weight: 600;
  }

  &__caption {
    margin: 10px 0 5px;
  }

  &__timer-caption {
    margin: 5px 0 5px;
  }
}



